html {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

input,
textarea,
select,
button {
  outline: none;
  font-weight: normal;
}

textarea {
  resize: none;
}

input {
  line-height: normal;
}

label,
button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

:root {
  --theme: #ffffff;
  --menu: #eeeeee;
  --line: #ff4203;
  --active-menu: #ff4203;
  --text-color: #000;
  --header-bgc: rgba(252, 253, 253, 0.89);
  --btn-color: #3734a9;
  --border-partner: #e3e6e9;
  --testimonial--color: #797979;
  --name-color: #453232;
  --border-item: #ededed;
  --box-shadow1: rgba(49, 49, 49, 0.0392593);
  --box-shadow2: rgba(49, 49, 49, 0.0240741);
}

[data-theme=dark] {
  --theme: #1f2028;
  --menu: #34353f;
  --line: #03ff89;
  --active-menu: #03ff89;
  --text-color: #fff;
  --header-bgc: rgba(31, 32, 40, 0.89);
  --btn-color: #5b56f3;
  --border-partner: #4a4b4b;
  --testimonial-color: #acacac;
  --name-color: #8a8282;
  --border-item: #535353;
  --box-shadow1: rgba(239, 239, 239, 0.0392593);
  --box-shadow2: rgba(239, 239, 239, 0.0240741);
}

html {
  font-size: 6.25%;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-thumb {
  background-image: linear-gradient(-45deg, #6a5af9, #d66efd);
  border-radius: 50px;
}

body::-webkit-scrollbar-track {
  background-color: #fafafa;
}

body {
  font-size: 16rem;
  min-height: 100vh;
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  background-color: var(--theme);
  overflow-x: hidden;
  user-select: none;
}

.container {
  max-width: 1280rem;
  margin: 0 auto;
  padding: 0 15px;
}

.decor-left {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}

.decor-right {
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
}

.curve {
  position: absolute;
  top: -30px;
  left: 200px;
  z-index: -1;
  fill: #cdb1b1;
}

.line-effect {
  width: 0;
  height: 5rem;
  background-color: var(--line);
  position: fixed;
  pointer-events: none;
  z-index: 999999;
  transition: 0.6s linear;
}

.toggle {
  position: fixed;
  z-index: 99;
  left: 10px;
  bottom: 10px;
  width: 80px;
  height: 34px;
  border-radius: 34px;
}
.toggle__input {
  display: none;
}
.toggle__input:checked + .toggle__label {
  background-color: #eee;
}
.toggle__input:checked + .toggle__label .toggle__slider {
  transform: translate(44px, -50%);
  background-color: #1f2028;
}
.toggle__label {
  display: inline-block;
  width: 100%;
  height: 100%;
  border-radius: inherit;
  background-color: #999;
  position: relative;
  transition: 0.3s linear;
  cursor: pointer;
}
.toggle__slider {
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
  width: 26px;
  height: 26px;
  border-radius: 50%;
  background-color: #fff;
  transition: 0.3s linear;
}

.scroll__top {
  position: fixed;
  z-index: 99;
  border: none;
  outline: none;
  right: 10px;
  bottom: 10px;
  font-size: 30px;
  width: 50px;
  height: 50px;
  border-radius: 4px;
  background-color: #e01d1d;
  color: #ffffff;
  box-shadow: 0 0 0 15px transparent;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s ease-in-out;
}
.scroll__top:hover {
  box-shadow: 0 0 0 0 #e01d1d;
}
.scroll__top:active {
  background-color: #fff4f4;
  color: #000;
}

.show-btn-scroll {
  visibility: visible;
  opacity: 1;
}

@media screen and (max-width: 1023.98px) {
  .line-effect {
    display: none;
  }

  .decor-left img {
    width: 90rem;
  }

  .decor-right img {
    width: 90rem;
  }
}
@media screen and (max-width: 479.98px) {
  .decor-left img {
    width: 50rem;
  }

  .decor-right img {
    width: 50rem;
  }
}
.btn {
  display: inline-block;
  border: 2px solid var(--btn-color);
  border-radius: 10rem;
  color: var(--btn-color);
  font-size: 16rem;
  text-align: center;
  font-weight: 700;
  padding: 14rem 37rem;
}

.btn-primary {
  color: #fff;
  border: 2px solid transparent;
  background-color: var(--btn-color);
  transition: 0.3s linear;
}
.btn-primary:hover {
  background-color: transparent;
  border: 2px solid var(--btn-color);
  color: var(--btn-color);
}

.heading {
  font-size: 54rem;
  font-weight: 800;
  line-height: 1.1851851852;
}

.text {
  font-weight: 500;
  font-size: 18rem;
  line-height: 1.6666666667;
  color: var(--text-color);
}

.text-small {
  font-weight: 400;
  font-size: 12rem;
  line-height: 2.1666666667;
  color: #fff;
}

@media screen and (max-width: 1279.98px) {
  .heading {
    font-size: 40rem;
  }
}
@media screen and (max-width: 1023.98px) {
  .heading {
    font-size: 30rem;
  }
}
.header {
  --fontsize-header: 24rem;
  padding: 15rem 0;
  position: sticky;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  background-color: var(--header-bgc);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: stretch;
  flex-direction: row;
  flex-wrap: nowrap;
}
.header__logo a {
  font-weight: 900;
  font-size: var(--fontsize-header);
  text-transform: uppercase;
  color: var(--text-color);
}
.header__auth {
  display: flex;
  align-items: center;
  justify-content: stretch;
  flex-direction: row;
  flex-wrap: nowrap;
  gap: 20rem;
  margin-left: 100rem;
}
.header__signup {
  padding: 8rem 20rem;
  position: relative;
  border-radius: 0;
  transition: 0.3s linear;
}
.header__signup::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  transition: 0.5s;
  bottom: -6rem;
  right: -6rem;
  border-bottom: 3px solid transparent;
  border-right: 3px solid transparent;
}
.header__signup::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  transition: 0.5s;
  top: -6rem;
  left: -6rem;
  border-top: 3px solid transparent;
  border-left: 3px solid transparent;
}
.header__signup:hover::before, .header__signup:hover::after {
  width: calc(100% + 12rem);
  height: calc(100% + 12rem);
  border-color: var(--btn-color);
}
.header__start {
  margin-left: -4rem;
  position: relative;
  padding: 4rem;
  display: inline-block;
  background-color: transparent;
}
.header__start:hover::before {
  width: 100%;
  height: 96%;
  opacity: 1;
  transition: width 0.2s ease-in, height 0.15s 0.2s linear, opacity 0s;
}
.header__start:hover::after {
  width: 100%;
  height: 96%;
  opacity: 1;
  transition: width 0.2s 0.35s linear, height 0.15s 0.5s ease-out, opacity 0s 0.3s;
}
.header__start span {
  display: inline-block;
  position: relative;
  color: #ffffff;
  background-color: var(--btn-color);
  font-size: 16rem;
  font-weight: bold;
  top: 0;
  left: 0;
  width: 100%;
  padding: 10rem 20rem;
  text-align: center;
  transition: 0.3s;
}
.header__start::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
  top: 0;
  right: 0;
  border-top: 3px solid var(--btn-color);
  border-left: 3px solid var(--btn-color);
  transition: width 0.2s 0.5s ease-out, height 0.15s 0.35s linear, opacity 0s 0.7s;
}
.header__start::after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  border-bottom: 3px solid var(--btn-color);
  border-right: 3px solid var(--btn-color);
  width: 0;
  height: 0;
  opacity: 0;
  background-color: transparent;
  z-index: 2;
  transition: width 0.2s 0.15s linear, height 0.15s ease-in, opacity 0s 0.35s;
}
.header__toggle {
  margin-left: auto;
  display: none;
  color: var(--text-color);
}

.menu {
  list-style: none;
  margin-left: auto;
  display: flex;
  align-items: stretch;
  justify-content: stretch;
  flex-direction: row;
  flex-wrap: nowrap;
}
.menu__item {
  display: flex;
  align-items: center;
  justify-content: stretch;
  flex-direction: row;
  flex-wrap: nowrap;
  position: relative;
  padding: 0 28rem;
  cursor: pointer;
}
.menu__link {
  font-size: 18rem;
  font-weight: 700;
  line-height: 20rem;
  color: var(--text-color);
  transition: 0.25s linear;
}
.menu__link.active {
  color: var(--active-menu);
}
.menu__link.active::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: currentColor;
  opacity: 0.125;
  border-radius: 5rem;
}

@media screen and (max-width: 1023.98px) {
  .header__auth {
    flex-direction: column;
    align-items: flex-start;
    padding-left: 20rem;
    margin-left: 0;
    margin-top: 20rem;
  }
  .header__toggle {
    display: block;
    font-size: var(--fontsize-header);
    cursor: pointer;
  }

  .menu {
    gap: 0;
    margin: 0 auto;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    width: 300rem;
    flex-direction: column;
    background-color: var(--menu);
    z-index: 99999;
    align-items: stretch;
    transition: all 0.2s linear;
    transform: translateX(100%);
  }
  .menu.is-show {
    transform: translateX(0);
  }
  .menu__item {
    padding: 0;
  }
  .menu__link {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: stretch;
    flex-direction: row;
    flex-wrap: nowrap;
    padding: 20rem;
  }
  .menu__link.active {
    color: var(--text-color);
  }
  .menu__link::before {
    display: none;
  }
  .menu__link:hover {
    background-color: var(--btn-color);
    color: #fff;
  }
}
@media screen and (max-width: 479.98px) {
  .menu {
    width: 200rem;
  }
  .menu__link {
    padding: 15rem;
  }

  .header {
    --fontsize-header: 18rem;
  }
}
